export default {
    actions: {
       async fetchCards(ctx){
            let res = await fetch('http://localhost:3000/log')
            const cards = await res.json()
            ctx.commit('updateCards', cards)
        },
    },
    mutations: {
        updateCards(state, cards) {
            state.cards = cards
        },
    },
    state: {
        cards: []
    },
    getters: {
        allCards(state){
            return state.cards
        }
    }
}