export default {
    actions: {
        async fetchCurrentUser(ctx, id){
            let res = await fetch("http://localhost:3000/getuser?id="+id, {
                                    method: 'GET',
                                    headers: {
                                    'Content-Type': 'application/json;charset=utf-8'
                                    },
                                    params: {id: id}
                                    })
            const user = await res.json()
            ctx.commit('setUser', user)                 
                                                                                    
        }                          
    },
    mutations: {
        setUser(state, user) {
            state.user = user
        },
    },
    state: {
        user: {}
    },
    getters: {
        currentUser(state){
            return state.user
        }
    }
}