export default {
    actions: {
       async fethLogin(ctx){
            let res = await fetch('http://localhost:3000/login')
            const user = await res.json()
           
            ctx.commit('setUser', user)
        },
        checkLocalStore() {
            
        }
    },
    mutations: {
        setUser(state, user) {
            state.user = user
        },
    },
    state: {
        user: {
            type: "",
            id: null,
            email: "",
            token: false,
            name: ""
        }
    },
    getters: {
        getUser(state){
            return state.user
        }
    }
}