export default {
    actions: {
       async fetchUsers(ctx){
            let res = await fetch('http://localhost:3000/getusers')
            const users = await res.json()
            ctx.commit('updateUsers', users)
          
        },
    },
    mutations: {
        updateUsers(state, users) {
            state.users = users
        },
    },
    state: {
        users: []
    },
    getters: {
        allUsers(state){
            return state.users
        }
    }
}